:root {
    --checkpot: #7ed084;
    --header_background: #7ed084;
    --button_primary_background: #7ed084;
}

.ActionSheetItem--default {
    color: #7ed084 !important;
    color: var(--checkpot) !important;
}

.TabbarItem--selected {
    color: #7ed084;
}

img.logo {
    vertical-align: middle;
}

.button-icon {
    margin-right: 10px;
    vertical-align: bottom;
}

.Alert {
    width: 90%;
}

.Alert__content {
    font-size: 14px;
}

.Alert__btn--cancel {
    color: #7ed084 !important;
}
.icon {
    margin-right: 5px;
    vertical-align: bottom;
}

.icon-span {
    margin-right: 5px;
    vertical-align: bottom;
}

.thumb-group {
    border-radius: 20px 20px 10px 10px;
}

.thumb-gallery {
    border-radius: 20px 20px 0 0;
}

.thumb-gallery .Gallery__slide {
    border-radius: 20px 20px 0 0;
}

.Group::before {
    display: none;
}

.Group::after {
    display: none;
}

.Tooltip__content {
    background: #ff6d48;
    font-size: 13px;
}

.Tooltip__corner {
    background-image: url(data:image/svg+xml;base64,);
}

.Gallery__bullets {
    margin-left: 20px;
    -webkit-justify-content: left;
            justify-content: left;
}

.Gallery__bullets--checkpot .Gallery__bullet {
    background-color: var(--white);
}

.Gallery__bullet--active {
    opacity: 1;
    background-color: var(--checkpot) !important;
}

.thumb-icon {
    margin-right: 5px;
    vertical-align: bottom;
}

.thumb-icon-cell {
    vertical-align: bottom;
    font-size: 12px;
    display: inline-block;
    width: 25%;
    text-align: left;
    text-align-last: left;
}

.thumb-icon-container {
    text-align: justify;
    text-align-last: justify;
}

.thumb-header {
    margin-top: 5px;
}

#clock_16 {
    color: #46d582;
}

#gift_16 {
    color: #3b9cff;
}

#smile_16 {
    color: #fbca69;
}

#trophy_16 {
    color: #ff6d48;
}
.main-header {
    
    display: contents;
    background: var(--checkpot);
}

.main-header-default {
    height: 44px;
    background: var(--checkpot);
}

.main-header-title {
    height: 44px;
    background: var(--checkpot);
}

.header-title {
    background: var(--checkpot);
    color: white;
    font-weight: 500;
    font-size: 20px;
}


.thumb-past-container > button {
    background: #ffe43e;
    color: black;
    width: 50%;
}

.thumb-past-container > span {
    -webkit-align-items: right;
            align-items: right;
    margin-left: 30px;
    color: #76787a;
    vertical-align: center;
}

.thumb-past-icon {
    color: #99a2ad !important;
    margin-right: 5px;
    margin-bottom: -2px;
    vertical-align: center;
}

.win-header {
    text-align: center;
    color: white;
}

.win-share-btn {
    background: #ffe43e;
}

.win-share-btn .Button__content{
    color: black;
    font-weight: 650 !important;
    font-size: 16px;
    text-transform: uppercase;
}

.win-panel .Panel__in {
    background-image: url(/static/media/background_win.4cdbdcfb.png);
}
#label_14 {
    color: #7ed084;
}

.icon-span {
    font-size: 14px;
}

.product-stat {
    width: 33.33%;
    display: inline-block;
    padding: 0px;
    text-align: center;
}

.product-stat span {
    font-weight: 600;
}

.product-stat .Cell__description {
    color: black;
}

.garant-title {
    color: #909499;
    text-transform: uppercase;
}

.garant-description {
    white-space: normal;
    font-size: 17px;
}
